import React from "react";
import { connect } from "react-redux";
class WordRecords extends React.Component {
	constructor(props) {
		super(props);
		this.word = this.props.wordRecords.word;
		this.records = JSON.parse(this.props.wordRecords.records);
	}
	componentDidMount() {
		// this.props.WordRecords();
	}
	render() {
		return (
			<div>
				<span>{this.word}</span>
				<span>{this.records}</span>
			</div>
		);
	}
}

const mapStateToProps = state => {
	return {};
};

const mapDispatchToProps = dispatch => {
	return {};
};
export default connect(mapStateToProps, mapDispatchToProps)(WordRecords);



