import React from "react";
import { connect } from "react-redux";
import WordRecords from "./WordRecords.js";
class WordsRecords extends React.Component {
	constructor(props) {
		super(props);
		this.wordsRecords = this.props.wordsRecords;
	}
	componentDidMount() {
		// this.props.WordsRecords();
	}
	render() {
		this.wordsRecords.map(wordRecords => {
			let word = wordRecords.word;
			let records = wordRecords.records;
			return <WordRecords word={word} records={records} />;
		});
	}
}

const mapStateToProps = state => {
	return {
		wordsRecords: state.wordsRecords
	};
};

const mapDispatchToProps = dispatch => {
	return {};
};
export default connect(mapStateToProps, mapDispatchToProps)(WordsRecords);



